                <v-col
                  cols="12"
                  sm="6"
                >
                  <v-text-field
                    v-model="form.id"
                    :rules="rules.id"
                    color="purple darken-2"
                    label=""
                    required
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                >
                  <v-text-field
                    v-model="form.passwd"
                    :rules="rules.passwd"
